@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

body {
  margin: 0;
}

.absolute-block-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.block-center {
  margin: auto;
}

.text-center {
  text-align: center;
}

.btn {
  display: inline-block;
  font-weight: 400;
  color: #212529;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 10px 13px;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .3rem;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.btn-outline-dark {
  color: #343a40;
  border-color: #535353;
}

.btn-outline-dark:hover {
  border-color: #adadad;
}

.text-shadow-sm {
  text-shadow: 0 0 5px rgba(0, 0, 0, .6);
}

.box {
    background-color: white;
    border-radius: 30px;
    
    height: auto;
    width: 80vw;
    z-index: 50;
}

.header {
    -webkit-margin-before: 0.67em;
            margin-block-start: 0.67em;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    font-size: calc(28px + (48 - 28) * ((100vw - 300px) / (1600 - 300)));
    font-weight: 500;
}

.subheader {
    margin: .1em 0 0 0;
    font-size: calc(12px + (28 - 12) * ((100vw - 300px) / (1600 - 300)));
    color: black;
    opacity: 0.7;
}

.shadow-big {
    box-shadow:
            0 0 2.2px rgba(0, 0, 0, 0.06),
            0 0 5.3px rgba(0, 0, 0, 0.07),
            0 0 10px rgba(0, 0, 0, 0.09),
            0 0 17.9px rgba(0, 0, 0, 0.11),
            0 0 33.4px rgba(0, 0, 0, 0.13),
            0 0 50px rgba(0, 0, 0, 0.16)
;
}

.payment-option {
    position: relative;
    height: 6vh;
    width: 75%;
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
    border-radius: 15px;
    padding: 1px;
}

.payment-option img {
    height: 50%;
    pointer-events: none;
}

.payment-overlay {
    background-color: black;
    opacity: 0;
    transition: 0.3s;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    z-index: 50;
}

.payment-overlay:hover {
    cursor: pointer;
    opacity: 0.05;
}

.payment-overlay:active {
    cursor: pointer;
    opacity: 0.15;
}

/* ----------- TVs ----------- */
@media screen and (min-width: 2200px) {
    .box {
        width: 20vw !important;
    }
}

/* ----------- Laptops ----------- */
@media screen and (min-width: 1400px) and (max-width: 2199px) {
    .box {
        width: 30vw !important;
    }
}

/* ----------- Ipad Big ----------- */
@media screen and (min-width: 981px) and (max-width: 1399px) {
    .box {
        width: 40vw !important;
    }
}

/* ----------- Ipad Small ----------- */
@media screen and (min-width: 768px) and (max-width: 980px) {
    .box {
        width: 50vw !important;
    }
}
.bg {
    width: 100vw;
    height: 100vh;
    background: #373B44;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(#3967b1, #2f344b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */



}
.welcome-card-container {
    width: 100vw;
    height: 40vh;
    position: relative;
    box-shadow: 0 0 calc(.5em + 2px) rgba(0, 0, 0, .5);
}

.welcome-card {
    width: 100%;
    height: 100%;
    /* Background Image */
    background-image: url(/berlm-website/static/media/coolboicode.e2c0abc3.png);
    background-size: cover;
    background-position: center;
    /* Center Children */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.bg-overlay {
    width: 100%;
    height: 100%;
    background-color: navy;
    /*background-image: linear-gradient(#23232a, #000000);*/
    opacity: 0.1;
    position: absolute;
}

.welcome-card * {
    color: white;
    z-index: 10;
    margin: 0 0 0 0;
}
 
.welcome-card p {
    padding-top: 10px;
}

.welcome-title {
    font-size: 32px;
    padding-bottom: 5px;
}

.welcome-subtitle {
    font-size: 24px;
    padding-top: 5px;
}

/* ----------- TVs ----------- */
@media screen and (min-width: 2200px) {
}

/* ----------- Laptops ----------- */
@media screen and (min-width: 1400px) and (max-width: 2199px) {
    .welcome-title {
        font-size: 60px !important;
    }

    .welcome-subtitle {
        font-size: 35px !important;
    }
}

/* ----------- Ipad Big ----------- */
@media screen and (min-width: 982px) and (max-width: 1399px) {
    .welcome-title {
        font-size: 55px !important;
    }

    .welcome-subtitle {
        font-size: 40px !important;
    }
}

/* ----------- Ipad Small ----------- */
@media screen and (min-width: 768px) and (max-width: 981px) {
    .welcome-title {
        font-size: 40px !important;
    }
    
    .welcome-subtitle {
        font-size: 30px !important;
    }
}


